@import url(https://fonts.googleapis.com/css2?family=Nunito&family=Nunito+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  text-align: left;
}

h1 {
  padding: 0;
  margin-top: 0.4em;
  margin-left: 4.5vh;
  font-size: 35vh;
  color: #011F4B;
  text-align: justify;
  line-height: 120%;
  font-family: 'Nunito', sans-serif;
}

pre {
  padding: 0;
}
.container {
  display: grid;
}

.header {
  font-size: 12vh;
  color: #011F4B;
  text-align: left;
  margin-bottom: 0.6em;
}

.info > img {
  padding-right: 4em;
  max-width: 100%;
  height: auto;
  float: right;
}

.text {
  width: 67%;
  float: left;
}

.text > p {
  font-family: 'Nunito', sans-serif;
  text-align: left;
  font-size: 4vh;
  line-height: 2;
  margin-left: 4.5vh;
  width: inherit;
  color: #03396C;
  margin-bottom: 1.2em;
}




.container {
  display: grid;
}

.header {
  font-size: 12vh;
  color: #011F4B;
  text-align: left;
  margin-bottom: 0.6em;
}

#background-image {
  -webkit-filter: saturate(99%) brightness(30%) hue-rotate(215.68deg);
          filter: saturate(99%) brightness(30%) hue-rotate(215.68deg);
  width: 65em;
  height: 8.5em;
  object-fit: cover;
  border-radius: 1em;
  margin-bottom: 1.75em;
}

#project-item{
  position: relative;
}

#title, #description {
  position: absolute;
  background: none;
}

#title {
  top: 0.25em;
  left: 7.5em;
  width: 11em;
  font-size: 3.8em;
  font-family: 'Nunito', sans-serif;
  font-weight: 500;
  color: #B3CDE0;
  text-align: right;
}

#description {
  font-size: 20px;
  color: #6497B1;
  top: 55%;
  left: -12.5%;
  text-align: right;
  width: 100%;
}
#contact-header {
  text-align: center;
  font-size: 4em;
  margin-top: 2em;
  width: 97%;
}

#contact-text {
  text-align: center;
  width: 150%;
  white-space: pre-line;
  margin: 0;
  display: block;
}

.inner > a:hover {
  opacity: 0.5;
}

.inner > a > img {
  margin-right: 2em;
  margin-left: 1em;
  x: 50%;
  width: 3.5em;
  z-index:1;
}

@media(max-width: 600px){
  #social-images {
    margin-top: 20em;
  }
}
.outer {
  position: relative;
}

.inner {
  position: absolute;
  width: 100%;
  background-color: rgba(0,0,0, 0);
  margin-top: 9em;
  height: 25%;
}

button {
  padding: .35em;
  text-align: center;
  font-size: 1.5em;
  text-decoration: none;
  border-radius: 0.45em;
  border-color: #6497B1;
  border-style: solid;
  border-width: 1.5px;
  margin-top: 4em;
  width: 7.5em;
  height: 3em;
}

button > a {
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: #6497B1;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
}

button:hover {
  color: #B3CDE0;
  background-color: #011F4B;
  border-color: #B3CDE0;
}

.footer {
  position: absolute;
  width: 100%;
  height: auto;
  border: none;
  bottom: 0;
  top: 30em;
  background-color: #011F4B;
  color: #011F4B;
}

.footer > p{
  color: #6497B1;
  background-color: #011F4B;
  padding: 1em;
  margin-top: 1em;
}


* {
  text-align: center;
  background-color: #B3CDE0;
  font-family: 'Nunito Sans', sans-serif;
}

.App {
  text-align: center;
  background-color: #B3CDE0;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

Home {
  margin-bottom: 90em;
}

.nav {
  overflow: hidden;
  background: #B3CDE0;
  position: fixed;
  top: 0;
  width: 100%;
  height: 2.5%;
  list-style-type: none;
  text-transform: lowercase;
  margin: 0em;
  padding: 1.25em;
}

@media (max-width: 700px){
  .nav {
    overflow: hidden;
    background: #203b4e;
    position: fixed;
    top: 0;
    width: 100%;
    height: 2.5%;
    list-style-type: none;
    text-transform: lowercase;
    margin: 0em;
    padding: 1.25em;
  }
}
#active {
  box-shadow: 0 0 10px rgba(0,0,0,0.4);  
}
.nav > li {
  float: right;
  overflow: hidden;
  background-color: #333;
  background: transparent;
  overflow: hidden;
  background-color: #333;
  margin-right: 2em;
}

li > a {
  padding:  1.05em;
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: #6497B1;
}

li > button {
  padding:  1.05em;
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: #6497B1;
}


li > a:hover {
  color: #005b96;
  cursor: pointer;
}

#home {
  float: left;
  font-weight: 20px;
  font-size: 1.15em;
}

#home > a, a:hover{
  color: #03396C;
}

#action {
  border-radius: 0.5em;
  border-color: #6497B1;
  border-style: solid;
  border-width: 1.5px;
  margin-right: 4em;
}

#action > a:hover {
  color: #B3CDE0;
  background-color: #011F4B;
  border-color: #B3CDE0;
}

